.blog__sec-heading h1 {
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -0.04em;
  text-align: left;
  width: 645px;
  height: 150px;
  padding: 0 0 0 30px;
}
.blog__container {
  display: flex;
}
.groubA {
  flex: 1;
  margin-right: 20px;
}
.groubB {
  flex: 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
@media screen and (min-width: 1101px) {
  .blog__container {
    margin: 20px 0 0 0;
  }
  .blog__container-artical-image {
    width: 100%;
  }
  .blog__container-artical-image {
    height: 42%;
  }
  .blog__container-artical-content {
    height: 58%;
    padding: 0 0 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .blog__container-artical-content div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 30%;
  }
  .groubB h3 {
    font-size: 18px;
  }
  .blog__container-artical-content p:last-child {
    display: flex;
    height: 20%;
    align-items: center;
  }
}

@media screen and (max-width: 1100px) {
  .blog__container-artical-content {
    padding: 10px 0px 0 10px;
  }
  .blog__container {
    padding: 0 20px;
  }
  .blog__sec-heading h1 {
    width: 90%;
    line-height: 40px;
    font-size: 38px;
    text-align: center;
    padding: 0;
    margin: auto;
  }
  .groubA h3 {
    font-size: 20px;
    line-height: 26px;
  }
  .groubB {
    grid-gap: 1rem;
  }
  .blog__container-artical-image {
    height: 42%;
  }
  .blog__container-artical-content {
    height: 58%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .blog__container-artical-content div {
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  .groubB .blog__container-artical .blog__container-artical-content div h3 {
    font-size: 12px;
    line-height: 16px;
  }
  .groubB .blog__container-artical .blog__container-artical-content div p {
    font-size: 10px;
    line-height: 16px;
    margin: 20px 0 5px 0;
    font-weight: 400;
  }
  .groubB
    .blog__container-artical
    .blog__container-artical-content
    p:last-child {
    font-size: 9px;
    line-height: 10px;
    padding: 0 0 10px 0;
    font-weight: 400;
  }
}
@media screen and (max-width: 600px) {
  .blog__sec-heading h1 {
    width: 90%;
    line-height: 40px;
    font-size: 32px;
    text-align: center;
    padding: 0;
    margin: auto;
  }
  .blog__container {
    padding: 0 20px;
    display: flex;
    flex-direction: column;
  }
  .groubA {
    margin-bottom: 10px;
    margin-right: 0px;
  }
  .groubB {
    display: flex;
    flex-direction: column;
  }
  .groubB .blog__container-artical {
    margin-bottom: 10px;
  }
  .blog__container-artical-content p {
    font-weight: 500;
    font-size: 10px;
    margin: 10px 0;
  }
  .blog__container-artical-content {
    padding: 0 15px;
  }
  .blog__container-artical-content h3 {
    font-size: 18px;
    line-height: 22px;
  }
  .blog__container-artical-content p:last-child {
    font-weight: 600;
    font-size: 12px;
    margin: 15px 0;
  }
}
