.gpt3__whatgpt3 {
  padding: 5rem 2rem;
  background: var(--color-footer);

  /* ff 3.6+ */
  background: -moz-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.gpt3__whatgpt3-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 100px 0;
  padding: 0 20px;
}
.gpt3__whatgpt3-heading h1 {
  font-family: var(--font-family);
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: left;
}
.gpt3__whatgpt3-heading p {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: #ff8a71;
}
.gpt3__whatgpt3-feature {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 30px;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  display: flex;
  justify-content: space-between;
}
.gpt3__whatgpt3-container {
  display: flex;
  flex-wrap: wrap;
}
.gpt3__features-container__feature {
  flex: 1;
}
.gpt3__whatgpt3-container .gpt3__features-container__feature-title {
  width: 100%;
}
.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
  width: 100%;
}
.gpt3__features-container__feature {
  padding: 0 20px;
}
@media screen and (max-width: 1100px) {
  .gpt3__features-container__feature-title {
    font-size: 11px;
  }
  .gpt3__features-container_feature-text {
    font-size: 12px;
    line-height: 20px;
  }
  .gpt3__whatgpt3-heading {
    margin: 50px 0;
  }
  .gpt3__whatgpt3-heading h1 {
    font-size: 22px;
  }
  .gpt3__whatgpt3-heading p {
    font-size: 12px;
  }
}

@media screen and (max-width: 800px) {
  .gpt3__whatgpt3 {
    padding: 2rem 1rem;
  }
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    flex-direction: column;
    padding: 0 0px;
  }
  .gpt3__features-container__feature-title {
    width: 100%;
    text-align: left;
    font-size: 21px;
  }
  .gpt3__features-container_feature-text {
    width: 100%;
    font-size: 14px;
    text-align: center;
    line-height: 25px;
  }
  .gpt3__whatgpt3-heading {
    margin: 50px 0;
    flex-direction: column;
    padding: 0 5px;
  }
  .gpt3__whatgpt3-heading h1 {
    line-height: 38px;
    text-align: center;
    font-size: 30px;
    padding: 2px 0;
  }
  .gpt3__whatgpt3-heading p {
    font-size: 13px;
  }
  .gpt3__whatgpt3-container .gpt3__features-container__feature-title {
    text-align: left;
    font-size: 15px;
  }
  .gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    font-size: 12px;
    line-height: 20px;
  }
  .gpt3__features-container__feature {
    margin: 16px 0;
  }
}
