.header-container {
  display: flex;
  align-items: flex-start;
}
.header-left {
  flex: 1;
}
.header-left h1 {
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -0.04em;
  text-align: left;
}
.header-left p {
  font-family: var(--font-family);
  font-size: 20px;
  font-weight: 400;
  line-height: 40px;
  letter-spacing: 0em;
  text-align: left;
  color: #81afdd;
  margin: 30px 0;
}

.header-right img {
  width: 100%;
}
.header-right {
  flex: 1 1;
  text-align: right;
}
.form {
  margin: 44px 0;
}
.form input[type="text"] {
  width: 80%;
  height: 73px;
  background-color: #052d56;
  outline: none;
  border: none;
  border-radius: 5px 0px 0px 5px;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 0em;
  text-align: left;
  padding: 15px;
  color: white;
}
.form input[type="submit"] {
  width: 20%;
  height: 73px;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 700;
  outline: none;
  border: none;
  color: white;
  background: #ff4820;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.peopel {
  display: flex;
  align-items: center;
}
.peopel p {
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 500;
  line-height: 38px;
  letter-spacing: 0em;
  text-align: left;
  color: white;
  padding-left: 10px;
  margin: 4px 0;
}
.section__padding {
  padding: 2rem 4rem !important;
}
.peopel img {
  width: 181.7px;
}
@media screen and (max-width: 1200px) {
  .header-left h1 {
    font-size: 44px;
    line-height: 58px;
  }
  .header-left p {
    font-size: 14px;
    line-height: 20px;
  }
  .form {
    margin: 10px 0;
  }
  .form input[type="text"] {
    width: 80%;
    height: 48px;

    font-family: var(--font-family);
    font-size: 12px;
  }
  .form input[type="submit"] {
    width: 20%;
    height: 48px;
    font-size: 12px;
    font-weight: 500;
  }
  .peopel img {
    width: 20%;
  }
  .peopel {
    align-items: center;
  }
  .peopel p.two {
    font-size: 10px;
  }
}
@media screen and (max-width: 933px) {
  .header-left h1 {
    font-size: 32px;
    line-height: 38px;
    margin: 10px 0 16px 0;
  }
  .header-left p {
    font-size: 13px;
    line-height: 18px;
  }
  .form input[type="text"] {
    width: 80%;
    height: 31px;
    font-size: 13px;
  }
  .form input[type="submit"] {
    margin-top: 5px;

    width: 20%;
    height: 30px;
    font-size: 11px;
  }
}
@media screen and (max-width: 800px) {
  .section__padding {
    padding: 1rem 0rem !important;
  }

  .header-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .header-left h1 {
    font-size: 27px;
    line-height: 31px;
    width: 90%;
    margin: 10px auto;
    text-align: center;
  }
  .header-left p {
    margin: 10px auto;
    width: 84%;
    font-size: 17px;
    line-height: 23px;
    text-align: center;
  }
  .form {
    display: flex;
    width: 84%;
    margin: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .form input[type="text"] {
    width: 98%;
    text-align: center;
    height: 57px;
    -o-border-radius: 5px;
    line-height: 27px;
    margin: 10px auto;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    border-radius: 5px;
  }
  .form input[type="submit"] {
    text-align: center;
    margin: auto;
    width: 100px;
    height: 38px;
    font-family: var(--font-family);
    font-size: 13px;
    font-weight: 900;
    -o-border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    border-radius: 5px;
  }
  .peopel {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 10px auto;
  }
  .peopel img {
    width: 30%;
    text-align: center;
    margin: auto;
  }
  .header-right {
    text-align: center;
    width: 70%;
  }
  .header-right img {
    width: 100%;
  }
  .header-left p.two {
    margin: 2px auto;
    width: 100%;
    font-size: 11px;
    line-height: 16px;
    text-align: center;
    padding: 0;
  }
}
