.blog__container-artical {
  width: 100%;
  height: 100%;
  background-color: var(--color-footer);
}
.blog__container-artical-image {
  width: 100%;
  height: 100%;
}
.blog__container-artical-image img {
  width: 100%;
  height: 100%;
}
.blog__container-artical-content {
}
.blog__container-artical-content p {
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 700;
  line-height: 35px;
  letter-spacing: 0em;
  text-align: left;
  color: white;
}
.blog__container-artical-content h3 {
  font-family: var(--font-family);
  font-size: 25px;
  font-weight: 800;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: white;
}
.blog__container-artical-content p:last-child {
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 700;
  line-height: 35px;
  letter-spacing: 0em;
  text-align: left;
  color: white;
}
