.possibility__container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.left__box {
  flex-basis: 38%;
}
.left__box img {
  width: 100%;
}
.right__box {
  flex-basis: 54%;
}
.right__box .p-one {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: #71e5ff;
}
.right__box h1 {
  font-family: var(--font-family);
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: left;
  margin: 50px 0;
}
.right__box .p-two {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0em;
  color: #81afdd;
  margin: 20px 0;
  text-align: left;
}
.right__box .p-three {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: #ff8a71;
}
@media screen and (max-width: 950px) {
  .left__box {
    flex-basis: 50%;
  }
  .right__box {
    flex-basis: 44%;
  }
  .right__box h1 {
    margin: 26px 0;
  }
  .right__box .p-two {
    padding-right: 10px;
  }
}
@media screen and (max-width: 650px) {
  .possibility__container {
    flex-direction: column;
  }
  .left__box {
    text-align: center;
    width: 85%;
    margin-bottom: 25px;
  }
  .right__box .p-one {
    text-align: center;
  }
  .right__box h1 {
    line-height: 30px;
    text-align: center;
    font-size: 26px;
    margin: 30px 0;
  }
  .right__box .p-two {
    font-size: 14px;
    text-align: center;
    line-height: 22px;
    padding: 0 10px;
    margin: 10px 0;
  }
  .right__box .p-three {
    text-align: center;
    font-size: 14px;
  }
}
