.features__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.right__sec {
  width: 35%;
  padding-left: 16px;
}
.left__sec {
  width: 61%;
}
.right__sec h1 {
  font-family: var(--font-family);
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: left;
}
.right__sec p {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;

  color: #ff8a71;
}
.left__sec .gpt3__features-container__feature {
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
}
.left__sec
  .gpt3__features-container__feature
  .gpt3__features-container__feature-title {
  margin: 0;
  width: 28%;
}
.left__sec
  .gpt3__features-container__feature
  .gpt3__features-container__feature-text {
  width: 70%;
}
.left__sec
  .gpt3__features-container__feature
  .gpt3__features-container__feature-title
  h1 {
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 800;
  line-height: 24px;
  letter-spacing: -0.04em;
  text-align: left;
}
.left__sec
  .gpt3__features-container__feature
  .gpt3__features-container__feature-title
  p {
  font-family: var(--font-family);
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
}
@media screen and (max-width: 850px) {
  .features__container {
    flex-direction: column;
  }
  .right__sec {
    width: 100%;
    padding: 0;
  }
  .right__sec h1 {
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    padding: 0 10px;
  }
  .right__sec p {
    text-align: center;
    font-size: 12px;
  }
  .left__sec {
    width: 100%;
  }
  .left__sec .gpt3__features-container__feature {
    flex-direction: column;
  }
  .left__sec
    .gpt3__features-container__feature
    .gpt3__features-container__feature-title {
    width: 100%;
  }
  .left__sec
    .gpt3__features-container__feature
    .gpt3__features-container_feature-text {
    text-align: left;
    width: 100%;
  }
}
