.gpt3__features-container__feature-title {
  font-family: var(--font-family);
  font-size: 24px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -0.04em;
  text-align: left;
  width: 30%;
  color: white;
  position: relative;
  padding: 10px 0;
}
.gpt3__features-container__feature-title::before {
  content: "";
  position: absolute;
  width: 38px;
  height: 3px;
  top: 0;
  left: 0;
  background: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%),
    linear-gradient(0deg, #ffffff, #ffffff);
}
.gpt3__features-container_feature-text {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  width: 68%;
  color: #81afdd;
}
