.footer__sec .footer__sec-heading h1 {
  font-family: var(--font-family);
  font-size: 62px;
  font-weight: 800;
  line-height: 75px;
  letter-spacing: -0.04em;
  text-align: center;
  margin-bottom: 60px;
}
.footer__sec-heading {
  text-align: center;
}
.footer__sec input {
  text-align: center;
  cursor: pointer;
  padding: 20px;
  border: 1px solid white;
  outline: none;
  color: white;
  margin-bottom: 40px;
  background-color: transparent;
}
.footer__sec-container {
  color: white;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 90%;
  padding: 120px 0 0 0;
  justify-content: space-between;
  margin: auto;
}
.footer__sec-container p {
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: left;
  margin-bottom: 15px;
  cursor: pointer;
}
.footer__sec-container .one img {
  margin: 0 0 25px 0;
}
.footer__sec-container .two {
  min-width: 140px;
  margin: 20px 20px;
}
.footer__sec-container .three {
  min-width: 140px;
  margin: 20px 20px;
}

.footer__sec-container .one {
  min-width: 140px;
  text-align: center;
  margin: 20px 20px;
}
.footer__sec-container .one p {
  text-align: center;
  cursor: auto;
}
.footer__sec-container .four {
  min-width: 140px;
  margin: 20px 20px;
}
.footer__sec-container h3 {
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0em;
  text-align: left;
  margin: 0 0 25px 0;
}
.end {
  color: white;
  margin-top: 80px;
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: center;
}
@media screen and (max-width: 1190px) {
  .footer__sec .footer__sec-heading h1 {
    font-size: 45px;
    line-height: 56px;
  }
  .footer__sec-container {
    padding: 65px 0 0 0;
  }
  .footer__sec-container .one {
    text-align: left;
    max-width: 50%;
  }
  .footer__sec-container .two {
    min-width: 32%;
  }
  .footer__sec-container .three {
    min-width: 50%;
  }
  .footer__sec-container .four {
    min-width: 32%;
  }
}
@media screen and (max-width: 625px) {
  .footer__sec .footer__sec-heading h1 {
    line-height: 50px;
    font-size: 38px;
    margin-bottom: 30px;
  }
  .footer__sec .one {
    text-align: center;
  }
  .footer__sec input {
    padding: 16px;
    font-size: 12px;
    margin-bottom: 0px;
  }
  .footer__sec-container {
    justify-content: center;
    align-items: center;
    padding: 60px 0 0 0;
  }
  .footer__sec-container p {
    text-align: center;
  }
  .footer__sec-container h3 {
    text-align: center;
  }
  .end {
    margin-top: 20px;
  }
  .footer__sec-container .one {
    min-width: 300px;
  }
  .footer__sec-container .two {
    min-width: 300px;
  }
  .footer__sec-container .four {
    min-width: 300px;
  }
  .footer__sec-container .three {
    min-width: 300px;
  }
  .end {
    text-align: center;
  }
}
