.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 4rem;
}
.gpt3__navbar a {
  text-decoration: none;
  color: white;
}
.gpt3__navbar-links {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
}
.gpt3__navbar-links__logo {
  margin-right: 2rem;
}
.gpt3__navbar-links__logo img {
  width: 62.56px;
  height: 16.02px;
}
.gpt3__navbar-links-container {
  display: flex;
  flex-direction: row;
}
.gpt3__navbar-sign {
  display: flex;
  justify-content: end;
  align-items: center;
}
.gpt3__navbar-links-container p,
.gpt3__navbar-sign p {
  font-family: var(--font-family);
  font-size: 17px;
  font-weight: 900;
  line-height: 25px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
  cursor: pointer;
  margin: 0 1rem;
}
.gpt3__navbar-sign button {
  background-color: #ff4820;
  width: 152px;
  height: 58px;
  padding: 0.5rem 1rem;
  color: #fff;
  -o-border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  border-radius: 5px;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  line-height: 25px;
  outline: none;
  border: none;
  cursor: pointer;
}
.gpt3__navbar-menu_container {
  background-color: var(--color-footer);
  color: white;
  text-align: right;
  padding: 15px 15px;
}
.gpt3__navbar-menu {
  display: none;
}
.gpt3__navbar-menu {
  position: relative;
}
.gpt3__navbar-menu_container {
  position: absolute;
  right: 0;
  top: 110%;
  width: 200px;
}
.gpt3__navbar-menu_container-links p {
  margin: 10px;
}
.gpt3__navbar-menu_container-links p a {
  cursor: pointer;
}
.gpt3__navbar-menu-sign {
  padding: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.gpt3__navbar-menu-sign p {
  cursor: pointer;
}
.gpt3__navbar-menu-sign button {
  outline: none;
  border: none;
  color: white;
  cursor: pointer;
  background-color: #ff4820;
  padding: 10px;
  -o-border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  border-radius: 5px;
}
@media screen and (max-width: 1050px) {
  .gpt3__navbar-links__logo {
    margin-right: 1.5rem;
  }
  .gpt3__navbar-links {
    justify-content: space-around;
  }
  .gpt3__navbar-links-container p {
    font-size: 18px;
    margin: 0 0.5rem;
  }
  .gpt3__navbar {
    padding: 1rem 0.5rem;
  }
  .gpt3__navbar-sign p {
    margin: 0 0.5rem;
  }
  .gpt3__navbar-sign button {
    padding: 0.5rem 0.5rem;
  }
  .gpt3__navbar-sign button {
    width: 85px;
  }
}
@media screen and (max-width: 850px) {
  .gpt3__navbar-menu {
    display: none;
  }
  .gpt3__navbar-links {
    justify-content: flex-start;
  }
  .gpt3__navbar-links__logo {
    margin-right: 1.5rem;
  }
  .gpt3__navbar-links-container p {
    font-size: 16px;
    margin: 0 0.5rem;
  }
  .gpt3__navbar {
    padding: 1rem 0.5rem;
  }
  .gpt3__navbar-sign p {
    margin: 0 1rem;
  }
  .gpt3__navbar-sign button {
    padding: 0.5rem 0.5rem;
  }
  .gpt3__navbar-sign button {
    width: 85px;
  }
}
@media screen and (max-width: 730px) {
  .gpt3__navbar-menu {
    display: block;
  }
  .gpt3__navbar {
    padding: 1rem 3rem;
  }
  .gpt3__navbar-links-container {
    display: none;
  }
  .gpt3__navbar-sign {
    display: none;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar-menu {
    display: block;
  }
}
