.cta__sec {
  -o-border-radius: 10px;
  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  border-radius: 10px;
  padding: 30px 20px;
}
.cta__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cta__container .text {
  width: 70%;
  padding: 0 10px;
}
.cta__container .text p {
  font-family: var(--font-family);
  font-size: 12px;
  font-weight: 500;
  line-height: 30px;
  margin: 10px 0 10px 0;

  letter-spacing: 0em;
  text-align: left;
}
.cta__container .text h1 {
  font-family: var(--font-family);
  font-size: 24px;
  font-weight: 800;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: left;
  margin: 10px 0 10px 0;
}
.cta__container input {
  font-weight: 600;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: black;
  width: 18%;
  color: white;
  height: 59px;
  border-radius: 30px;
}
@media screen and (max-width: 1050px) {
  .cta__container input {
    width: 17%;
  }
  .cta__container .text {
    width: 70%;
  }
  .cta__container .text h1 {
    line-height: 28px;
  }
}
@media screen and (max-width: 850px) {
  .cta__container {
    flex-direction: column;
  }
  .cta__container .text {
    width: 100%;
  }
  .cta__container .text h1 {
    font-size: 23px;
    line-height: 30px;
    text-align: center;
  }
  .cta__container .text p {
    text-align: center;
  }
  .cta__container input {
    width: 45%;
    font-size: 14px;
    height: 46px;
  }
}
